@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html{
  margin:0;
  padding:0;
  font-family:'Open Sans';
}

*{
  box-sizing:border-box;
}

a{
  color:black;
}

main{
  min-height:100vh;
  display:grid;
  place-items:center;
  align-content:center;
  padding:20vh;
}


.project{
    min-height:600px;
    background:#eee;
    position:relative;
    display:grid;
    place-items:center;
    overflow:hidden;
    cursor: pointer;
}

.project img{
    width:100%;
    height:100%;
    object-fit: cover;
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
    -webkit-transition:.4s ease;
    transition:.4s ease;
}


.project-display-content{
    position:absolute;
    display:grid;
    place-items:center;
    background:rgba(0,0,0,.4);
    width:100%;
    height:100%;
    color:white;
    -webkit-transition:.5s ease;
    transition:.5s ease;
}

.project h1{
    font-size:1.4rem;
    margin:0;
    padding:0;
}

.admin-icons{
    position:absolute;
    right:1rem;
    bottom:1rem;
    display:grid;
    grid-auto-flow:column;
    justify-content:end;
    grid-gap:1rem;
    gap:1rem;
}

.project:hover > .project-display-content{
    background:rgba(0,0,0,0);
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
}

.project:hover > img{
    -webkit-transform:scale(1);
            transform:scale(1);
}

@media (max-width:1000px){
    .project{
        min-height:400px;
    }
  }
main.projects {
  padding: 15vh 0 0 0;
  align-items: flex-start;
  align-content: flex-start;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
}

.add {
  position: fixed;
  width: 100vw;
  left: 0;
  top: 10vh;
  height: 5vh;
  display: grid;
  place-items: center;
  background: gray;
  box-shadow: 0 0 22px 2px rgba(255, 255, 255, .4);
  z-index: 1;
}

@media (max-width:1000px) {
  .projects-container {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }
}
main.project-details{
    justify-content:start;
    padding:0;
}
.project-details img {
    width:32vw;
}

.pager{
    position:fixed;
    width:100vw;
    left:0;
    top:10vh;
    height:5vh;
    background:#eee;
    display:grid;
    padding: 0 1rem 0 1rem;
    grid-auto-flow:column;
    grid-gap:4rem;
    gap:4rem;
    align-items:center;
    justify-content:center;
    z-index:1;
}

.description img{
    width:100%;
    margin:1rem 0 1rem 0;
}

.project-content{
    padding:20vh;
    min-height:100vh;
}
.header-container{
    position:fixed;
    width:100vw;
    height:10vh;
    background:black;
    display:grid;
    place-items:center;
    z-index:2;
}

.burger{
    display:none;
    z-index:2;
    position:absolute;
    cursor: pointer;
}

header{
    display:grid;
    grid-auto-flow:column;
    justify-content:center;
    align-items:center;
    grid-gap:1rem;
    gap:1rem;
    z-index:1;
}

header a{
    text-decoration: none;
    color:#eee;
}

[aria-current='page'], .active {
    font-weight:bold;
}

@media (max-width:1000px){
    header{
        width:100%;
        -webkit-transform:translateY(-100%);
                transform:translateY(-100%);
        grid-auto-flow:row;
        align-content:center;
        min-height:100vh;
        -webkit-transition:.4s ease;
        transition:.4s ease;
        background-color:black;
    }
    header a {
        font-size:2rem;
    }
    header.visible{
        -webkit-transform:translateY(0);
                transform:translateY(0);
    }
    .burger{
        display:block;
    }
    main{
        padding: 20vh 5vh;
    }
}

main.login{
}

.login img{
    width:200px;
    margin-bottom:1rem;
}
input,
textarea,
select,
button,
.button {
    font-size: 1rem;
    padding: .6rem;
    display: block;
    width: 250px;
    margin-bottom:1rem;
}

.project-images {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    justify-content: left;
    grid-gap: .2rem;
    gap: .2rem;
    margin: 1rem 0 1rem 0;
}

.project-images > div {
    display: grid;
    place-items: center;
    position: relative;
}

.project-images>div h3 {
    position: absolute;
    color: white;
    font-size: 2rem;
    ;
}


.project-images img {
    height:250px;
    width: 100%;
    object-fit:cover;
}

.edit-icons {
    cursor: pointer;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

button,
.button {
    cursor: pointer;
    background-color: black;
    color: white;
    display: block;
}

.button {
    background: white;
    border: 1px solid #111;
    color: #111;
    text-align: center;
    margin-top: 1rem;
}

textarea {
    width: 100%;
    min-height: 8rem;
}

.edit {
    -webkit-animation: slideIn .6s ease;
            animation: slideIn .6s ease;
}

.checks {
    display: grid;
    background: #f4f4f8;
    grid-template-columns: 1fr 4fr;
    margin: 1rem 0 1rem 0;
    padding: .6rem;
}

@-webkit-keyframes slideIn {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideIn {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@media (max-width:1000px){
    input,
    textarea,
    select,
    button,
    .button {
        width: 100%;
    }    
}
